<template>
  <div class="list-item">
    <div class="wrapper">
      <div @click="toggleCheck">
        <p-check class="p-icon p-round p-smooth p-bigger" color="success" :checked="checked">
          <i slot="extra" class="icon mdi mdi-check"></i>
        </p-check>
      </div>
      <p class="todo">{{ todo }}</p>
      <p class="delete-button" @click="deleteTodo">X</p>
    </div>
  </div>
</template>

<script>
  import Icon from 'vue-awesome/components/Icon'

  export default {
    name: "CheckListItem",
    props: ['todo', 'checked', 'id'],
    components: {
      Icon
    },

    data() {
      return {
      }
    },

    methods: {
      toggleCheck() {
        this.$store.commit('toggleCheck', this.id);
      },

      deleteTodo() {
        this.$store.commit('deleteTodo', this.id);
      }
    }
  }
</script>

<style scoped>
  .list-item {
    /*min-width: 80px;*/
    min-height: 65px;
    /*max-width: 700px;*/
    max-height: 100px;
    width: 100%;
    font-size: 3rem;
    color: #ffffff;
    /*border: 0.5px solid #cccccc;*/

    display: flex;
    /*align-items: center;*/
    font-family: 'Do Hyeon', cursive;
    box-shadow: 0 1px 2px rgba(255, 255, 255, 0.19), 0 6px 6px rgba(255, 255, 255, 0.23);
    transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
  }

  .list-item:hover {
    box-shadow: 0 3px 5px rgba(255, 255, 255, 0.25), 0 10px 10px rgba(255, 255, 255, 0.22);
    transform: scale(1.2);
  }

  .wrapper {
    display: inherit;
    /*flex: 1;*/
    width: 100%;
    align-items: center;
    justify-content: space-between;
    /*text-align: right !important;*/
  }

  .todo {
    /*align-self: center ;*/
  }

</style>



