<template>
  <div class="fullpage-container">
    <div class="fullpage-wp" v-fullpage="options" ref="mainpage">
      <div class="page-1 page">
        <div class="part-1">
          <DrawnText/>
          <p class="left-time">{{days}} : {{hours}} : {{minutes}} : {{seconds}}</p>
        </div>
      </div>
      <div class="page-2 page">
        <p class="header-text" v-animate="{value: 'fadeIn', delay: 0}">가고싶은 해변을 살펴보세요.</p>
        <div class="attraction" v-animate="{value: 'slideInUp', delay: 300}">
          <div class="region-wrapper">
            <div class="region jangho-beach" @click="getContents('jangho')">
              <p>장호항</p>
            </div>

            <div class="region chuam-beach" @click="getContents('chuam')">
              <p>추암 해수욕장</p>
            </div>

            <div class="region mangsang-beach" @click="getContents('mangsang')">
              <p>망상 해수욕장</p>
            </div>

            <div class="region sinnam-beach" @click="getContents('sinnam')">
              <p>신남 해수욕장</p>
            </div>

            <div class="region mureong-valley" @click="getContents('mureong')">
              <p>무릉계곡</p>
            </div>
          </div>
        </div>
      </div>
      <div class="page-3 page">
        <p class="page-3header-text" v-animate="{value: 'fadeIn', delay: 50}">물건들은 다 챙기셨나요?</p>
        <div class="todo-list-wrapper">
          <div class="todo-list" v-for="todo of this.$store.state.todos">
            <CheckListItem :todo="todo.title" :checked="todo.completed" :id="todo.id"/>
          </div>
          <div class="webflow-style-input">
            <input v-model="input_text" placeholder="체크 할 일을 추가하세요." @keyup.enter="addTodo">
          </div>
        </div>
      </div>
    </div>

    <div class="modal-wrapper">
      <sweet-modal ref="modal" modal-theme="dark" overlay-theme="dark" :title="blog_article_title">
        <p v-html="blog_article">
        </p>
        <img :src="blog_image">
      </sweet-modal>
    </div>
  </div>
</template>

<script>
  import DrawnText from './DrawnText'
  import CheckListItem from './partial/CheckListItem'
  import {SweetModal} from 'sweet-modal-vue'

  export default {
    name: "MainPage",
    components: {
      DrawnText,
      CheckListItem,
      SweetModal
    },

    mounted() {
      this.leftDays = setInterval(this.updateCountDown, 1000);
    },

    data() {
      return {
        options: {
          start: 0,
          dir: 'v',
          duration: 500,
          beforeChange: function (prev, next) {
          },
          afterChange: function (prev, next) {
          }
        },

        targetDate: new Date("Aug 18, 2018 00:00:00").getTime(),

        days: 0,
        hours: 0,
        minutes: 0,
        seconds: 0,
        leftDays: 0,

        input_text: '',

        blog_article_title: '',
        blog_article: '',
        blog_image: [],
      }
    },

    methods: {
      moveNext() {
        this.$refs.example.$fullpage.moveNext(); //Move to the next page
      },

      updateCountDown() {
        const now = new Date().getTime();
        const distance = this.targetDate - now;

        this.days = Math.floor(distance / (1000 * 60 * 60 * 24));
        this.hours = Math.floor((distance % (1000 * 60 * 60 * 24)) / (1000 * 60 * 60));
        this.minutes = Math.floor((distance % (1000 * 60 * 60)) / (1000 * 60));
        this.seconds = Math.floor((distance % (1000 * 60)) / 1000);
      },

      addTodo() {
        if (this.input_text.length === 0) {
          return;
        } else {
          this.$store.commit('addTodo', this.input_text);
          this.input_text = '';
        }
      },

      getContents(region) {
        const contents = this.$store.state.blog_article.find(contents => {
          return contents.region === region;
        });

        this.blog_article_title = contents.title;
        this.blog_article = contents.article;
        this.blog_image = contents.image;
        this.$refs.modal.open();
      }
    }
  }
</script>

<style lang="scss" scoped>
  $input-background: rgba(57, 63, 84, 0.8);
  $input-text-inactive: #7881A1;
  $input-text-active: #BFD2FF;

  @keyframes gradient {
    0%{background-position:0 0}
    100%{background-position:100% 0}
  }

  .webflow-style-input {
    position: relative;
    display: flex;
    flex-direction: row;
    width: 90%;
    max-width: 800px;
    margin: 0 auto;
    border-radius: 2px;
    padding: 1.4rem 2rem 1.6rem;
    background: $input-background;
    &:after {
      content: "";
      position: absolute;
      left: 0px;
      right: 0px;
      bottom: 0px;
      z-index: 999;
      height: 2px;
      border-bottom-left-radius: 2px;
      border-bottom-right-radius: 2px;
      background-position: 0% 0%;
      background: linear-gradient(to right, #B294FF, #57E6E6, #FEFFB8, #57E6E6, #B294FF, #57E6E6);
      background-size: 500% auto;
      animation: gradient 3s linear infinite;
    }
  }

  .webflow-style-input input {
    width: 100%;
    /*flex-grow: 1;*/
    color: $input-text-active;
    font-size: 1.8rem;
    line-height: 2.4rem;
    vertical-align: middle;
    /*background: rgba(57, 63, 84, 0.8);*/
    &::-webkit-input-placeholder {
      color: $input-text-inactive;
    }
  }

  input {
    border-style: none; background: transparent; outline: none;
  }

  .left-time {
    color: #ffffff;
    font-size: 3rem;
    text-align: center;
    font-family: 'Avenir', Helvetica, Arial, sans-serif;
    font-weight: 700;
  }

  .page-3 {
    display: flex;
    flex-direction: column;
    align-items: center;
    justify-content: space-around;
    background: url("http://drive.google.com/uc?export=view&id=1rbrBDw6uSdob6egzSILcda1Kce7LGORJ");
    background-size: cover;

  }

  .todo-list-wrapper {
    display:inherit;
    /*flex: 5;*/
    flex-direction: column;
    width: 50%;
    max-width: 800px;
  }


  .todo-list {
    width: 100%;
    height: 100%;
    margin-bottom: 1rem;
  }

  .fullpage-container {
    width: 100%;
    height: 100vh;
  }

  .page-1 {
    background: url('http://drive.google.com/uc?export=view&id=1EY1VAn36ztmIjC6UOxnj4-AoLjkWuk6T');
    background-size: cover;
  }

  .page-2 {
    background: url('http://drive.google.com/uc?export=view&id=1BWwLtGIs42nLTZp0UDVZvbLc1cpNy2OZ');
    background-size: cover;

  }

  .header-text {
    padding-top: 3rem;
    font-family: 'Nanum Brush Script', cursive;
    font-size: 4rem;
    text-align: center;
    color: #ffffff;
    margin-bottom: -3rem;
  }

  .page-3header-text {
    font-family: 'Nanum Brush Script', cursive;
    font-size: 4rem;
    text-align: center;
    color: #ffffff;
  }

  .attraction {
    display: flex;
    height: 100%;
    align-items: center;
  }

  .region-wrapper {
    display: inherit;
    flex-wrap: wrap;
    flex: 1;
    height: 40%;
    align-items: stretch;
  }

  .region {
    flex: 1;
    min-height: 100px;
    height: available;
    text-align: center;
    filter: opacity(0.8);
    transition: all 0.3s cubic-bezier(.25, .8, .25, 1);
  }

  .region p {
    width: 100%;
    padding-top: 2rem;
    font-size: 2rem;
    font-weight: 700;
    color: white;
  }

  .region:hover {
    transform: scale(1.2);
  }

  .jangho-beach {
    background: url("http://drive.google.com/uc?export=view&id=1Jxrk1_ofoEraoADVLVmsd7TNzq364jxI");
    background-size: cover;
  }

  .chuam-beach {
    background: url("http://drive.google.com/uc?export=view&id=1WLtFVSBKTBakm0L33c3pcCN-rebluVIV");
    background-size: cover;
  }

  .mangsang-beach {
    background: url("http://drive.google.com/uc?export=view&id=12OJXyROKSn64j8avL5uVxCjLhq-cGpMn");
    background-size: cover;
  }

  .sinnam-beach {
    background: url("http://drive.google.com/uc?export=view&id=1HcPcLT4wDc2BUyPCPBOGCrbcpMUz3nu-");
    background-size: cover;
  }

  .mureong-valley {
    background: url("http://drive.google.com/uc?export=view&id=1GGTK-aYHRlfpm-hP2clCJZQHafcFD42M");
    background-size: cover;
  }
</style>



