import Vue from 'vue'
import Vuex from 'vuex'

Vue.use(Vuex);

export const store = new Vuex.Store({
  state: {
    todos: [
      {id: 1, title: '옷 챙기기', completed: false},
      {id: 2, title: '양치도구 챙기기', completed: false},
      {id: 3, title: '즐길 준비하기', completed: true},
    ],

    blog_article: [
      {id: 1, region: 'jangho', title: '장호항', article: '<p>동그랗고 새하얀 해안선이 아름다워 한국의 나폴리로 불린다는 삼척 장호항 이번 강원도 여행에 찾고 싶은 곳 중 한 곳이었지요<br>국내 여름휴가지 중에 당연 손에 꼽힐 유명한 장소로 이미 잘 알려진 장소이지요 그래서 처음 가보는 이곳이 어떤 매력이 있는지 저도 궁금했던 1인이었답니다.<br>삼척 시내에서 가까운 곳에 위치해 연인들의 데이트 코스로도 유명한지 평일에 갔는데 다정한 연인들도 많이 보입니다<br>활처럼 둥글게 휘어진 백사장과 해돋이가 일품인 곳으로 소문난 곳이라는데 아쉽게도 이번에 도착했을 땐 점심시간쯤 되었네요<br>다음에 다시 기회가 되면 이곳 근처에서 하루 숙박하고 아름다운 일출의 장관도 담아보고 싶어졌습니다<br>기대하고 도착했던 첫인상은<br>여느 동해 바다 해수욕장처럼 넓은 백사장이 펼쳐진 모습은 아니었지만 하얀 모래사장과 기암괴석으로 둘러싸인 아담하게 보이는 바다 풍경은 가슴으로 폭 안길 거 같은 그런 포근한 모습으로 다가왔습니다. 아담한 해변에 평일인데도 옹기종기 모여있는 사람들의 모습이 참 정겹게 느껴지네요<br><br></p>', image: 'https://postfiles.pstatic.net/MjAxODA3MDZfMTgy/MDAxNTMwODA3NTAyNzkx.jB6B7-snRO_i6YVmJI5pNV4qvvFgDPBcGU4vJCsCXNog.XM9u2sUikJj9vERozCo-EA65GYAW8HzfeFDyR0KxhBQg.JPEG.tourjeonju/166A8849.JPG?type=w966'},
      {id: 2, region: 'mangsang', title: '망상 해수욕장', article: '<p> 동해안에는 여름과 겨울 모두 들러도 좋은 바다와 드라이브 코스가 있다.<br> 강원도 동해안 일대에서도 동해-삼척을 잇는 해안은 가파르고 험하여 물이 깨끗하고 푸르다는 점에서 가장 인상깊은 해안이다.<br> 이곳들 중에서 동해시 북쪽 바다를 따라 연결된 약 6km의 해안도로는 현지인들 사이에서 유명할 뿐, 서울과 수도권을 비롯한 대도시 사람들에겐 상대적으로 잘 모르는 곳으로 통한다.<br> 이 해안에서 가장 유명한 곳이 망상해수장이다. 이미 일찌감치 국민관광지로 지정되어 놀이시설까지 들어선 유명 해수욕장이지만, 최근에 오토캠핑장 때문에 전국적으로 더 알려졌다. </p>', image: 'https://t1.daumcdn.net/cfile/tistory/2319443F5932367217'},
      {id: 3, region: 'chuam', title: '추암 해수욕장', article: '<p> 삼척여행지중 두번째로 다녀온 곳은 촛대바위로 유명한 추암 해수욕장이었어요.<br> 추암해수욕장은 자그마한 해수욕장이지만 해안산책로가 상당히 멋있었고 한국ㄱ의 석림 능파대가 있는 곳이라 주변 경관이 환상적인 곳이었어요.<br> 능파대는 인근 하천과 파랑에 의해 운반된 모래가 쌓여 육지와 연결된 육계도와 촛대바위같은 암석기둥들을 포함한 지역을 총칭하는 말이구요<br> 추암 해수욕장에서는 촛대 바위뿐만아니라<br> 잠자는 거인바위, 코끼리바위, 양머리바위등 다양한<br> 모양의 암석들을 볼 수 있는곳이었어요. </p>', image: 'http://blogfiles.naver.net/MjAxNzAzMTdfMzIg/MDAxNDg5NzM5NDAwMTUz.tgyWRSlRPdi-jkWQY_10uucFhuwGK8Xs5k2Ulhr0bz0g.z0v6mcAtHw4oNkIGFOPRVFIfGmjdqOBXzAdMVIZc2dAg.JPEG.mirage14/20170311-DSC05706.jpg'},
      {id: 4, region: 'sinnam', title: '신남 해수욕장', article: '<p> 강원 삼척시 원덕읍 길남리 신남해수욕장<br> 개수대 1개와 샤워장은 해수욕장 뒤편으로 반박집에서 유료로 운영을 하고있다.<br> 온수가 나오는지는 개별 확인<br> <br> 해수욕장 뒤편으로 민박집들이 저렴한 가격으로 이용할수 있다는 것도 큰 장점.<br> 바가지 요금이 없다고 하니 직접 이용해보자<br> 수세식 화장실/개수대 온수 불가/샤워실 유료/전기 불가/화로대 가능/해수욕장 텐트 설치가능/수심이 얇아서 물놀이 좋음 </p>', image: 'https://mblogthumb-phinf.pstatic.net/20130807_230/syszero_1375844163258nOoOf_JPEG/DSC01368.JPG?type=w2'},
      {id: 5, region: 'mureong', title: '무릉계곡', article: '<p> 전국 국민관광지 제1호인 동해시 무릉계곡을 다녀왔습니다.<br> 무릉계곡은 기암괴석으로 아주 멋진 모습을 보여주면서<br> 쌍폭포와 용추폭포 등 다양한 형태의 폭포가 많은 곳으로도 유명한데<br> 폭포는 물의 양이 많을 때 가 보아야 제대로 된 멋진 모습을 볼 수 있습니다.<br> 그래서 오랫동안 비가 내린 뒤 날씨가 청명하여 들려본 것입니다.<br> </p>', image: 'https://postfiles.pstatic.net/MjAxNzA4MjhfMjQy/MDAxNTAzOTA2NTUyOTYw.2ILvYctOeFLB3R2bUXnuIr6oycHHNrclsZsF78XJy9Ig.BnnHTTh_ZRNHgfJuegJ6LJbNGmDJght2IKxR4UWLv0Qg.JPEG.daichung/20170825_113755_HDR.jpg?type=w966'},
    ]
  },

  mutations: {
    addTodo(state, payload) {
      const _next_id = state.todos ?  Math.max(state.todos.map(todo => todo.id)) + 1 : 1;
      const _todo = {id: _next_id, title: payload, completed: false};
      state.todos = [...state.todos, _todo];
    },

    toggleCheck(state, payload) {
      const _todo_id = payload;
      state.todos = state.todos.map(todo => {
        return todo.id === parseInt(_todo_id) ? Object.assign({}, todo, {completed: !todo.completed}) : todo;
      });

      console.log(state.todos);
    },

    deleteTodo(state, payload) {
      const _todo_id = payload;
      state.todos = state.todos.filter(todo => {
        return todo.id !== parseInt(_todo_id);
      });
    },

  }
})



